.banner {
    position: relative;
    top: 10px;
    height: 75vh; /* 전체 화면의 75%로 조정 */
    min-height: 360px;
    background-size: cover;
    background-position: center top; /*  배너 사진 가운데 위쪽으로 조정*/
    display: flex;
    align-items: flex-end;
}

.content {
    position: relative;
    bottom: 100px;
    padding: 40px;
    max-width: 700px;
}

.title {
    margin: 0 0 12px 0;
    font-size: 50px;
    font-weight: 800;
    color: white;
}

.desc {
    margin: 0 0 20px 0;
    font-size: 14px;
    line-height: 1.5;
    opacity: 0.9;
    color: white;
    font-size: 20px;
}

.buttons {
    display: flex;
    gap: 10px;
    margin-bottom: 16px;
}

.button {
    height: 60px;
    border: 0;
    border-radius: 6px;
    padding: 10px 16px;
    cursor: pointer;
    font-size: 30px;
    font-weight: 700;
}

.play {
    width: 150px;
    background: white;
    color: black;
}

.info {
    width: 200px;
    background: rgba(109, 109, 110, 0.7);
    color: white;
}

.fadeBottom {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 120px;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(17, 17, 17, 0.9),
        #111
    );
}

@media (max-width: 768px) {
    .banner {
        height: 360px;
    }
    .contents {
        padding: 96px 16px 24px;
        max-width: none;
    }
    .title {
        font-size: 1.8rem;
    }
    .description {
        font-size: 0.9rem;
        line-height: 1.34;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }
    .buttons {
        gap: 8px;
    }
    .buttons button {
        padding: 8px 14px;
        font-size: 14px;
    }
}